html {
  overflow-y: scroll;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  background-color: #fff9f9;
  padding: 0 10px;
}

@media (min-width: 511px) {
  body {
    padding: 0 10%;
  }
}

@media (min-width: 800px) {
  body {
    padding: 0 15%;
  }
}

@media (min-width: 1000px) {
  body {
    padding: 0 20%;
  }
}

@media (min-width: 1000px) {
  body {
    padding: 0 23%;
  }
}

@media (min-width: 1400px) {
  body {
    padding: 0 28%;
  }
}

@media (min-width: 1700px) {
  body {
    padding: 0 33%;
  }
}

button {
  border-style: none;
}

button:focus {
  outline: 0;
}



