.sectionTitle {
  border-bottom: 2px solid #d5d5d5;
  position: relative;
}
.sectionTitle h2 {
  text-decoration: none;
  color: #000000;
  font-size: 1.3em;
  text-align: center;
  margin: 18px 0;
  position: relative;
}

.contact-links {
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.sectionTitle a {
  text-decoration: none;
  color: #000000;
  display: block;
  position: absolute;
  left: 0;
  width: 30px;
  height: 30px;
  cursor: pointer;
  z-index: 200;
  bottom: 16px;
}

.site-links a {
  color: #ce3728;
  text-decoration: none;
}
.site-links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-top: 2rem;
}
.site-links button {
  background: none;
  border: none;
  text-decoration: none;
  color: #ce3728;
  cursor: pointer;
  padding: 0;
  font-family: "Lato", sans-serif;
  font-size: 15px;
}

.contact-me {
  background-color: #f4f4f4;
  padding: 1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-bottom: 10px;
  font-size: 15px;
}
.contact-me h3 {
  text-align: center;
  margin-top: 0;
  margin-bottom: 0.5rem;
}
@media (min-width: 500px) {
  .sectionTitle {
    display: block;
  }
  .contact-me {
    padding: 1.3rem 4rem;
  }
  .contact-me h3 {
    margin-bottom: 0.7rem;
  }
}

@media (min-width: 708px) {
  .contact-me {
    padding: 1.4rem 22%;
    font-size: 17px;
    margin-bottom: 20px;
  }

  .site-links button {
    font-size: 17px;
  }
  .contact-me h3 {
    font-size: 1.3em;
  }

  .sectionTitle h2 {
    font-size: 1.8em;
  }
  .sectionTitle a {
    bottom: 20px;
  }
}



