.Header {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  height: 95vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  color: #ce3728;
  z-index: 200;
  padding-bottom: 2.5px;
}

.photo {
  margin-top: 10vh;
  width: 150px;
  height: 150px;
}

.introText {
  padding: 0 20px;
}

.linkToSection {
  -webkit-text-decoration: "none";
          text-decoration: "none";
}

.big-header-line {
  width: 100%;
  height: 60%;
  border-bottom: 1.5px solid #ce3728;
  display: block;
  margin: auto 0;
}

.ShortHeader .big-header-line {
  display: none;
}

.ShortHeader .myNameText {
  margin: 18px 0;
  font-size: 1.7em;
}
.myNameText {
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.ShortHeader {
  height: 125px;
  padding: 0;
  border-bottom: 2px solid #d5d5d5;
}

.ShortHeader .btnRow {
  margin-top: 0px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
}

/*
.ShortHeader .btnRow .outer {
  width: 90px;
