.ShortHeader .introText {
  display: none;
}

.ShortHeader .photo {
  display: none;
}

.btnRow {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 35px;
}
.Header .btnRow .linkToSection:nth-child(1) {
  margin-right: 25px;
}

.inner {
  border: 3px solid #ce3728;
  color: #ce3728;
  height: 40px;
  width: 125px;
  text-align: center;
  padding: 10px 0px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  bottom: 1.8px;
  background-color: #fafafa;
  right: 1.8px;
  font-weight: bold;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}

.outer {
  position: relative;
  width: 125px;
  height: 40px;
  background-color: #e7e9da;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 3px solid #ce3728;
  border-right: 3px solid #ce3728;
  cursor: pointer;
  -webkit-transition: border-color 0.15s ease, background-color 0.15s ease-out;
  -o-transition: border-color 0.15s ease, background-color 0.15s ease-out;
  transition: border-color 0.15s ease, background-color 0.15s ease-out;
}
.outer:hover .inner {
  bottom: 0px;
  right: 0px;
  border-color: #fafafa;
  color: #fafafa;
  background-color: #ce3728;
}

.selectedInner {
  bottom: 0px;
  right: 0px;
  border-color: #fafafa;
  color: #fafafa;
  background-color: #ce3728;
}

.selectedOuter {
  border-color: transparent;
  background-color: #c8cace;
}

.outer:hover {
  border-color: transparent;
  background-color: #c8cace;
}

.inner {
  -webkit-transition: bottom 0.15s ease-out, right 0.15s ease-out,
    border-color 0.15s ease-out, color 0.15s ease-out,
    background-color 0.15s ease-out;
  -o-transition: bottom 0.15s ease-out, right 0.15s ease-out,
    border-color 0.15s ease-out, color 0.15s ease-out,
    background-color 0.15s ease-out;
  transition: bottom 0.15s ease-out, right 0.15s ease-out,
    border-color 0.15s ease-out, color 0.15s ease-out,
    background-color 0.15s ease-out;
}

@media (min-width: 599px) {
  .myNameText {
    font-size: 2.2em;
  }

  .photo {
    margin-top: 15vh;
  }

  .ShortHeader .myNameText {
    margin: 15px 0;
    font-size: 2.2em;
  }

  .ShortHeader .btnRow {
    -ms-flex-pack: end;
        justify-content: flex-end;
    width: 300px;
  }

  .inner,
  .outer {
    width: 162.5px;
    height: 52px;
    font-size: 1.05rem;
  }
}

@media (min-width: 768px) {
  .ShortHeader {
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
  .inner,
  .outer {
    width: 187.5px;
    height: 60px;
    font-size: 1.2rem;
  }
  .introText {
    font-size: 1.5rem;
  }

  .ShortHeader .inner {
    width: 125px;
    height: 40px;
    font-size: 0.83rem;
  }
  .ShortHeader .outer {
    width: 125px;
    height: 40px;
  }
}

@media (min-width: 1300px) {
  .Header {
    height: 85vh;
  }

  .ShortHeader {
    height: 140px;
    padding: 0;
  }

  .photo {
    width: 165px;
    height: 165px;
    margin-top: 20vh;
  }
}



